<template>
    <div>
        <!-- button -->
        <div :style="block">
            <h4>button</h4>
            <button type="primary" htmlType="{ backgroundColor: 'yellow' }"
                >排成設定<icon type="setting" theme="filled"
            /></button>
        </div>

        <div :style="block">
            <dropdown :placement="bottomCenter" trigger="click">
                <button type="primary">
                    排成設定<a-icon type="setting" theme="filled"
                /></button>
                <a-menu> <!--slot="overlay"     v-slot:overlay-->
                    <a-menu-item @click="showModal" :style="{ textAlign: 'center' }">
                        設定排程
                    </a-menu-item>
                    <a-menu-item
                        @click="showDeleteConfirm"
                        :style="{ textAlign: 'center' }"
                    >
                        刪除排程
                    </a-menu-item>
                </a-menu>
            </dropdown>
        </div>

        <!-- setting button options -->
        <div :style="block">
            <h4>setting button options</h4>
            <a-button type="outlined"> 設定排程 </a-button>
            <br />
            <a-button type="outlined" @click="showDeleteConfirm"> 刪除排程 </a-button>
        </div>

        <!-- date picker and time picker -->
        <div :style="block">
            <h4>date picker and time picker</h4>
            <a-space>
                日期：<a-date-picker @change="onDateChange" /> 時間：<a-time-picker
                    use12-hours
                    format="h:mm a"
                    @change="onTimeChange"
                />
            </a-space>
        </div>

        <!-- card sidebar -->
        <div :style="block">
            <h4>card sidebar</h4>
            <a-radio-group v-model="value" @change="onScheduleChange">
                <a-radio :style="radioStyle" :value="1"> 一次性 </a-radio>
                <a-radio :style="radioStyle" :value="2"> 每日 </a-radio>
                <a-radio :style="radioStyle" :value="3"> 每週 </a-radio>
                <a-radio :style="radioStyle" :value="4"> 每月 </a-radio>
                <a-radio :style="radioStyle" :value="5"> 自訂 </a-radio>
            </a-radio-group>
        </div>

        <!-- card main -->
        <div :style="block">
            <h4>card main</h4>
            <a-radio-group v-model="value2" @change="onScheduleChange2">
                <a-radio :style="radioStyle" :value="1"> 從不結束 </a-radio>
                <a-radio :style="radioStyle" :value="2">
                    指定結束時間
                    <div v-if="value2 === 2">
                        <a-space>
                            日期：<a-date-picker @change="onDateChange" />
                            時間：<a-time-picker
                                use12-hours
                                format="h:mm a"
                                @change="onTimeChange"
                            />
                        </a-space>
                    </div>
                </a-radio>
            </a-radio-group>
        </div>

        <!-- dialog 設定排程-->
        <div :style="block">
            <h4>dialog</h4>
            <a-button type="primary" @click="showModal"> Open Modal </a-button>
            <a-modal
                v-model="showSettingCard"
                title="定期排成設定"
                okText="儲存排成"
                cancelText="取消"
                @ok="handleOk"
                width="600px"
                >
                <a-row>
                    <a-col :span="4">
                        <a-radio-group v-model="value" @change="onScheduleChange">
                            <a-radio :style="radioStyle" :value="1"> 一次性 </a-radio>
                            <a-radio :style="radioStyle" :value="2"> 每日 </a-radio>
                            <a-radio :style="radioStyle" :value="3"> 每週 </a-radio>
                            <a-radio :style="radioStyle" :value="4"> 每月 </a-radio>
                            <a-radio :style="radioStyle" :value="5"> 自訂 </a-radio>
                        </a-radio-group>
                    </a-col>
                    <a-col :span="2">
                        <a-divider type="vertical" :style="{ height: '270px' }" />
                    </a-col>
                    <a-col :span="18">
                        <a-space direction="vertical" size="large">
                            <a-row>
                                <a-space direction="vertical">
                                    <h4>開始時間</h4>
                                    <a-col offset="2">
                                        <a-space>
                                            <a-date-picker @change="onDateChange" />
                                            在<a-time-picker
                                            use12-hours
                                            format="h:mm a"
                                            @change="onTimeChange"
                                            />
                                            </a-space>
                                            </a-col>
                                        </a-space>
                                    </a-row>
                                <a-row>
                                <a-space direction="vertical">
                                    <h4>結束時間</h4>
                                    <a-col>
                                        <a-radio-group v-model="value2" @change="onScheduleChange2">
                                            <a-radio :style="radioStyle" :value="1">
                                                從不結束
                                            </a-radio>
                                            <a-radio :style="radioStyle" :value="2">
                                                指定結束時間
                                            </a-radio>
                                        </a-radio-group>
                                    </a-col>
                                    <a-col offset="2" v-if="value2 === 2">
                                        <a-space>
                                            <a-date-picker
                                            @change="onDateChange"
                                              :style="{ margin: '10px 0' }"
                                            />
                                            在<a-time-picker
                                                use12-hours
                                                format="h:mm a"
                                                @change="onTimeChange"
                                            />
                                        </a-space>
                                    </a-col>
                                </a-space>
                            </a-row>
                        </a-space>
                    </a-col>
                </a-row>
            </a-modal>
        </div>
    </div>
</template>

<script>
export default {
  name: 'scheduleModel',
  data () {
    return {
      value: 1,
      alue2: 1,
      showSettingCard: false,
      radioStyle: {
        display: 'block',
        height: '30px',
        lineHeight: '30px'
      },
      block: {
        margin: '30px'
      }
    }
  },
  methods: {
    onDateChange (date, dateString) {
      console.log(date, dateString)
    },
    onTimeChange (time, timeString) {
      console.log(time, timeString)
    },
    onScheduleChange (e) {
      console.log('radio checked', e.target.value)
    },
    onScheduleChange2 (e) {
      console.log('radio checked 2', e.target.value)
    },
    showDeleteConfirm () {
      this.$confirm({
        title: '確定要刪除此排程？',
        // content: 'Some descriptions',
        okText: '刪除排程',
        okType: 'danger',
        cancelText: '取消',
        onOk () {
          console.log('Delete')
        },
        onCancel () {
          console.log('Cancel')
        }
      })
    },
    showModal () {
      this.showSettingCard = true
    },
    handleOk (e) {
      console.log(e)
      this.showSettingCard = false
    }
  }
}
</script>
<style scoped></style>
